.contact-form{
	form{
		button{
	    	background: @color-primary-button;
	    	color: @color-white;
			float: right;
			font-size: 2rem;
			border-radius: 5px;
			box-shadow: 15px 7px 60px 0 @color-black;

	    	&:hover{
	    		background: @color-danger;
	    	}	

	    	&:focus{
	    		outline: none;
	    	}
    	}

    	.g-recaptcha{
    		float: left;
    		box-shadow: 15px 7px 60px 0 @color-black;
    	}

	    .fieldset{
			.clear;

	    	> div{
	    		&:first-child{
	    		    width: 35%;
				    margin-right: 5%;
				    float: left;
		    	}

		    	&:last-child{
	    		    width: 60%;
    				float: left;
		    	}
	    	}
	    }

	    .form-group{
	    	margin-bottom: 20px;

	    	label{
	    		font-size: 2rem;
	    		margin-bottom: 2px;
	    	}

	    	input[type="text"], input[type="email"], textarea{
    		    width: 100%;
			    margin: 0;
			    border: 0;
			    font-size: 2rem;
			    padding: 5px;
			    border-radius: 5px;
			    box-sizing: border-box;
			    border: 2px solid transparent;
			    box-shadow: 15px 7px 60px 0 @color-black;
			    font-family: inherit;

			    &:focus{
			    	border-color: @color-danger;
			    	outline: none;
			    }

	    	}

	    	textarea{
	    		height: 205px;
	    		resize: none;
	    	}
	    }
	}
}